@tailwind base;
@tailwind components;
@tailwind utilities;




/* ✅ Remove Default Margin & Set Global Styles */
body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
   height: 100%; /* ✅ ADD THIS LINE */
  overflow-x: hidden; /* ✅ Recommended */
}

/* ✅ Logo will be above the Navbar */
.logo-container {
  width: 100%;
  background-color: white; /* Ya jo bhi color chahiye */
  display: flex;
  justify-content: center;
  /* padding: 15px 0; */
  height: auto; /* ✅ Auto height so it adjusts */
}

/* ✅ Navbar will stick when scrolling */
.navbar {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #0A2647;
  z-index: 999;
  display: flex;
  justify-content: center;
  /* padding: 10px 0; */
}

/* ✅ Main Content Padding (To Avoid Overlap) */
.main-content {
  /* padding-top: 20px; 
  padding-bottom: 60px; */
  width: 100%;
  max-width: 100%;
}

/* ✅ Hero Section Fix */
.hero-section {
  /* margin-top: 20px;  */
  width: 100%;
  text-align: center;
}

img {
  transition: none;
}

img:hover {
  filter: none;
  opacity: 1;
}

